:root {
  --font-roboto: "Roboto", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --gray: hsl(0, 0%, 58%);
  --blue: hsl(197, 78%, 48%);
  --black: hsl(0, 0%, 15%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  color: var(--gray);
  font-family: var(--font-roboto);
}

h1,
h2,
h3,
p {
  margin: 0;
  line-height: 2;
  font-weight: 100;
}

p {
  font-weight: 300;
}

a {
  color: var(--gray);
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

.logo {
  font-size: 0;
}

.title {
  font-size: 3rem;
  color: var(--black);
}

.subtitle {
  font-size: 0.9375rem;
  font-weight: 400;
}
.subtitle span {
  margin: 0 0.5rem;
}

.text a {
  color: var(--blue);
}
.text .subtitle {
  font-weight: 700;
}

.link,
.link:hover {
  float: left;
  text-decoration: none;
}

.header .link {
  color: var(--black);
}
.header .link:last-child {
  float: right;
}
.header .link:not(:last-child) {
  margin-right: 1.875rem;
}

.album .link {
  font-size: 0;
}
.album .link:not(:last-child) {
  margin-right: 0.625rem;
}

.container {
  padding: 0 1.25rem;
  max-width: 72.5rem;
  margin: 0 auto;
}

.promo {
  background: url("../images/promo.jpg") no-repeat 50%/cover fixed;
  position: relative;
  min-height: 100vh;
}
.promo::before {
  content: "";
  min-width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
}
.promo.image::after {
  min-width: 80%;
  min-height: 18.75rem;
  background-image: url("../images/slogan.png");
}
.promo .arrow-down {
  position: absolute;
  left: 50%;
  bottom: 1.375rem;
  transform: translate(-50%, 0);
}
.promo .arrow-down .fas {
  font-size: 2.5rem;
}

.image::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: no-repeat 50%/contain;
}

.header {
  background-color: var(--white);
  position: relative;
  z-index: 1;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 5rem;
}
.header__content {
  min-width: 6.875rem;
}
.header__content:first-child {
  display: none;
}
@media (min-width: 71.875em) {
  .header__content:first-child {
    display: block;
  }
}
.header__content:last-child {
  text-align: right;
}

.album__block {
  display: flex;
}
@media (max-width: 87.4375em) {
  .album__block {
    flex-direction: column;
  }
}
.album__block:first-child .text {
  max-width: 24.0625rem;
  margin-bottom: 2rem;
}
.album__block:first-child .image {
  background-image: url("../images/album-1.jpg");
}
@media (max-width: 87.4375em) {
  .album__block:first-child .image {
    min-height: 37.5rem;
  }
}
.album__block:first-child .image::before {
  background-color: rgba(25, 49, 75, 0.9);
}
.album__block:first-child .image::after {
  min-width: 80%;
  min-height: 16.875rem;
  background-image: url("../images/disk.png");
}
.album__block:last-child {
  flex-direction: column;
}
@media (min-width: 71.875em) {
  .album__block:last-child {
    flex-direction: row-reverse;
  }
}
.album__block:last-child .text {
  max-width: 23.125rem;
}
.album__block:last-child .image {
  background-image: url("../images/album-2.jpg");
}
@media (max-width: 87.4375em) {
  .album__block:last-child .image {
    min-height: 37.5rem;
  }
}
.album__block:last-child .image::before {
  background-color: rgba(0, 0, 0, 0.3);
}
.album__block .image {
  display: flex;
  justify-content: center;
  align-items: center;
  background: 50%/cover;
  position: relative;
  min-width: 50%;
}
.album__block .image::before {
  content: "";
  min-width: 100%;
  min-height: 100%;
  position: absolute;
}
.album__block .image:hover {
  text-decoration: none;
}
.album__block .image:hover .fas {
  background-color: rgba(255, 0, 0, 0.8);
}
.album__block .image .fas {
  position: relative;
  font-size: 2.5rem;
  border-radius: 1.25rem;
  padding: 1.25rem 3.75rem;
  background-color: rgba(37, 37, 37, 0.8);
  color: var(--white);
}
.album__content {
  padding: 6.875rem 1.25rem;
  margin: 0 auto;
}
@media (max-width: 87.4375em) {
  .album__content {
    text-align: center;
  }
}
.album__item {
  display: flex;
  justify-content: center;
}
@media (min-width: 71.875em) {
  .album__item {
    display: block;
  }
}

.event {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  min-height: 16.25rem;
  margin: 1.875rem auto;
}
@media (max-width: 87.4375em) {
  .event {
    flex-direction: column;
    margin: 6.875rem 0;
  }
}

.clip {
  position: relative;
}
.clip__item {
  text-align: center;
  padding: 7.5rem 3.75rem;
  background: linear-gradient(90deg, #3359ac 0%, #1ba3d9 100%);
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
}
@media (min-width: 71.875em) {
  .clip__item {
    padding: 1.25rem 5rem;
  }
}
.clip__item:hover {
  background: linear-gradient(270deg, #3359ac 0%, #1ba3d9 100%);
  text-decoration: none;
}
.clip__item * {
  color: var(--white);
}
@media (max-width: 87.4375em) {
  .clip__item * {
    font-size: 1.5rem;
  }
}
.clip__content {
  display: flex;
  flex-wrap: wrap;
}
.clip__content img {
  flex-grow: 1;
}

.footer {
  display: flex;
  justify-content: space-between;
  filter: grayscale(100%);
  margin: 1.875rem auto;
}
@media (max-width: 87.4375em) {
  .footer {
    flex-direction: column;
  }
}
.footer__content {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media (max-width: 87.4375em) {
  .footer__content:first-child {
    flex-direction: column;
  }
}
@media (max-width: 87.4375em) {
  .footer__content:first-child .subtitle {
    margin: 1.875rem 0;
  }
}
@media (min-width: 71.875em) {
  .footer__content .logo {
    margin-right: 3.75rem;
  }
}
.footer__content .link:not(:last-child) {
  margin-right: 1.875rem;
}